import React from 'react'
import { Link } from 'react-router-dom'

const NavBar = () => (
<nav className="nav">
    <ul>
        <li>
            <Link to="/">Home</Link>
            <Link to="/about">About</Link>
            <Link to="/article">Article</Link>
            <Link to="/articles-list">Article list</Link>
        </li>
    </ul>
</nav>
)

export default NavBar